@import "var/colors";
@import "var/fonts";
@import "var/icons";

html{

}
body{
    font-size: 16px;
    color: $gray-800;
    background: $white;
    font-family: $font-family-system;
    overflow-x: hidden;
    position: relative;
    width: 100%;
    margin: 0 auto;
    transition: 0.3s ease transform;
}
@media screen and (max-width: 991px){
    body.--noScroll{
        overflow: hidden;
    }
    body.--active{
        transform: translateX(200px);
    }
}
@media screen and (min-width: 992px){
    body.submenu-active{
        overflow-y: scroll;
    }
}

body.in-kosik::after, body.ordering-process::after{
    content: "";
    height: 300px;
    width: 100%;
    display: flex;
    background-image: url("../img/bg.png");
    background-position: center top;
}

::selection {
    background: $green-200;
}

a{
    color: $green-700;
    text-decoration: none;
    transition: 0.3s ease all;

    &:hover, &:active, &:focus{
        color: $gray-800;
    }
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, 
.col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, 
.col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, 
.col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, 
.col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, 
.col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, 
.col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, 
.col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto{
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
}
.container {
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
}
.row{
    margin-right: -10px;
    margin-left: -10px;
}
@media (min-width: 1220px){
    .container {
        max-width: 1220px;
    }
}
@media (max-width: 1219px){
    .container {
        max-width: 100%;
    }
}
@media (max-width: 991px){
    .container {
        max-width: 100%;
        padding-right: 10px;
        padding-left: 10px;
    }
}

input:focus, button:focus{
    outline: none;
}

header + .content-wrapper .content-wrapper-in{
    padding: 0;
}

.col-xs-12{
    width: 100%;
    padding: 0 10px;
}
.no-display{
    display: none;
}

.content{
    width: 100%;
}
.type-post .content-wrapper-in{
    flex-wrap: nowrap;
}
.content > h4{
    display: none;
}
.in-klient .content, .in-nastaveni .content, .in-objednavky .content, 
.in-klient-objednavky .content, .in-klient-slevy .content, .in-klient-hodnoceni .content, .in-klient-doklady .content,
.in-klient-diskuze .content, .in-zapomenute-heslo .content, .in-dorucovaci-udaje .content{
    width: 75%;
}
@media screen and (max-width: 991px){
    .in-klient .content, .in-nastaveni .content, .in-objednavky .content, 
    .in-klient-objednavky .content, .in-klient-slevy .content, .in-klient-hodnoceni .content, .in-klient-doklady .content,
    .in-klient-diskuze .content, .in-zapomenute-heslo .content, .in-dorucovaci-udaje .content{
        width: 100%;
    }
}



.in-klient-slevy .content table{
    width: 100%;

    td{
        border: 1px solid $gray-400;
        padding: 10px;
    }
}
.in-klient-slevy .content table + p{
    color: $gray-700;
    margin-top: 10px 20px;
}

.middle-banners-wrapper .banner-wrapper{  
    height: auto;
}
.middle-banners-wrapper .banner-wrapper > span{  
    width: 100%;
}

.in-login{
    .content{
        width: 100%;
    }
    .content-inner{
        width: 100%;
        max-width: 700px;
        margin: 0 auto;
        box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.08);
        background: $white;
        padding: 40px;
    }
    .content-inner form .btn{
        width: 100%;
        background: $orange-700;
        color: $white;

        &:hover, &:active, &:focus{
            color: $white;
            background: $orange-800;
        }
    }
    .content-inner h1{
        text-align: center;
        position: relative;
        display: flex;
        justify-content: center;
        font-size: 24px;
        font-weight: 600;
        margin-bottom: 20px;
        padding-bottom: 15px;
        color: $gray-800;

        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 6px;
            width: 40px;
            background: $green-200;
        }
    }
    .content-inner h1.spaced{
        margin-top: 30px;
        padding-top: 40px;
        border-top: 1px solid $gray-400;
    }
}

@media screen and (max-width: 991px){
    .in-login{
        .content-inner{
            width: 100%;
            box-shadow: none;
            background: $white;
            padding: 0px;
        }
    }
}



