.in-login .content-inner h1.spaced, .in-login .content-inner h1.spaced+form{
    display: none;
}
.messages a{
    color: $white; 
}
.msg{
    width: 100%;
    padding: 20px 20px;
    background: $green-700;
    color: $white;   
    display: flex;
    text-align: left;
}
.msg-success::before{
    content: "";
    font-size: 24px;
}
.msg-error::before{
    content: "";
    font-size: 24px;
}
.msg-error{
    background: #c53030;
}
.msg-warning{
    background: $gray-300;
    color: $gray-900;
}
.messages{
    display: flex;
    position: fixed;
    bottom: 20px;
    top: auto;
    left: auto;
    right: 20px;
    width: 400px;
    text-align: center;
    z-index: 9;
    box-shadow: 0px 5px 5px rgba(0,0,0,0.08);
}
.in-klient, .in-objednavky{

    .content-inner h2{
        font-size: 18px;
    }
    .content-inner .table{
        word-break: break-word;
    }
    .content-inner .table th{
        border-bottom: 1px solid $gray-400;
        padding: 10px 10px;
        font-size: 14px;
    }
    .content-inner .table td{
        background: $gray-300;
        padding: 10px 10px;
        font-size: 14px;
    }
}

.in-registrace{
    .content-inner h1{
        position: relative;
        display: flex;
        justify-content: center;
        font-size: 24px;
        font-weight: 600;
        margin: 0 auto;
        margin-bottom: 20px;
        padding-bottom: 10px;
        max-width: 600px;
        text-align: center;
    
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            height: 6px;
            width: 40px;
            background: $green-200;
        }
    }
    #register-form{
        max-width: 700px;
        margin: 0 auto;
        background: $white;
        padding: 20px;
    }
    #register-form h4{
        text-align: center;
        position: relative;
        display: flex;
        font-size: 18px;
        font-weight: 600;
        margin-bottom: 20px;
        padding-bottom: 15px;
        color: $gray-800;
        width: 100%;
        margin-top: 20px;
    
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            height: 6px;
            width: 40px;
            background: $green-200;
        }
    }
    .box-squeeze{
        display: none;
    }
    .box-squeeze .form-group{
        display: flex;
        padding: 0;
    }
    
    #company-info, #shipping-address{
        display: none;
    }
    #company-info.visible, #shipping-address.visible{
        display: flex;
        flex-direction: column;
    }
    .submit-wrapper .btn-login{
        width: 100%;
        background: $orange-700;
        color: $white;

        &:hover, &:active, &:focus{
            color: $white;
            background: $orange-800;
        }
    }
}

.in-registrace #content-wrapper-wrap::after{
    content: "";
    height: 300px;
    width: 100%;
    position: sticky;
    bottom: 0;
    margin-top: -300px;
    z-index: -1;
    display: flex;
    background-image: url("../img/bg.png");
    background-position: center top;
}


.in-dekujeme .header-top{
    border-bottom: 1px solid $gray-400;
}
.ratingSection__content #ratingWrapper {
    display: flex;      
    width: 100%;
}
.ratingSection__content #ratingWrapper #rate-form{
    display: none;
}
.ratingSection__content #ratingWrapper .rate-wrap{
    width: 100%;
    flex-direction: row;
    margin-left: 60px;
    margin-bottom: 20px;
    padding: 30px 40px;
    padding-left: 0;
}
.ratingSection__content #ratingWrapper .rate-wrap .col-sm-6 {
    min-width: 50%;
    flex-basis: 50%;
    padding-bottom: 0px;
}
@media screen and (max-width: 991px){
    .ratingSection__content #ratingWrapper .rate-wrap{
        margin: 0;
        margin-bottom: 20px;
        padding: 20px;
    }
}